.card::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  height: 4px;
  background: linear-gradient(
    90deg in oklch,
    transparent 0%,
    var(--color-accent-main) 10%,
    oklch(0.95 0.02 0) 30%,
    var(--color-accent-mid) 50%,
    oklch(0.95 0.02 0) 70%,
    var(--color-accent-main) 90%,
    transparent 100%
  );
  border-radius: 32px;
  animation: shimmer 5s ease-in-out infinite;
  filter: blur(1px);
}
.card::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 32px;
  background: radial-gradient(
      circle at 25% 25%,
      color-mix(in srgb, oklch(0.75 0.15 145) 3%, transparent) 0%,
      transparent 70%
    ),
    radial-gradient(
      circle at 75% 75%,
      color-mix(in srgb, oklch(0.75 0.12 210) 2.5%, transparent) 0%,
      transparent 70%
