    background: radial-gradient(
      circle at 25% 25%,
      color-mix(in oklch, var(--color-accent-main) 3%, transparent) 0%,
      transparent 70%
    ),
    radial-gradient(
      circle at 75% 75%,
      color-mix(in oklch, var(--color-accent-mid) 2.5%, transparent) 0%,
      transparent 70%
    ),
    linear-gradient(
      45deg in oklch,
      transparent 0%,
      color-mix(in oklch, white 1%, transparent) 50%,
      transparent 100%
    );
  }
  pointer-events: none;
  z-index: 1;
}
.card:hover::before {
  background: linear-gradient(
    90deg in oklch,
    transparent 0%,
    color-mix(in srgb, oklch(0.75 0.15 145) 60%, transparent) 10%,
    color-mix(in srgb, white 80%, transparent) 30%,
    color-mix(in srgb, oklch(0.75 0.12 210) 70%, transparent) 50%,
    color-mix(in srgb, white 80%, transparent) 70%,
    color-mix(in srgb, oklch(0.75 0.15 145) 60%, transparent) 90%,
    transparent 100%
  );
  @supports (color: color-mix(in lab, red, red)) {
    background: linear-gradient(
    90deg in oklch,
    transparent 0%,
    color-mix(in oklch, var(--color-accent-main) 60%, transparent) 10%,
    color-mix(in oklch, white 80%, transparent) 30%,
    color-mix(in oklch, var(--color-accent-mid) 70%, transparent) 50%,
    color-mix(in oklch, white 80%, transparent) 70%,
    color-mix(in oklch, var(--color-accent-main) 60%, transparent) 90%,
