  animation: shimmer 2s ease-in-out infinite;
}
.bento-item::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 20px;
  background: radial-gradient(
    circle at 20% 20%,
    color-mix(in oklch, white 2%, transparent) 0%,
    transparent 60%
  );
